import React, { useEffect, useState } from 'react';

//  Componentes MUI
import { Box, Button, Typography } from '@mui/material';

export default function LocalInfoContainer() {

  const handleEntry = () => {
    // REDIRIGIR AL LOCAL
    window.location.href = '/contenido?id=k-001';
  }

  return (
    <Box
      sx={{ height: '100%', p: 3, textAlign: 'center', display: 'flex', flexFlow: 'column', justifyContent: 'center', alignItems: 'center' }}
      role="presentation"
    >
      <Box id="chat-window" className="scroller" xs={12} sx={{height:'95%', overflow:'hidden', overflowY:'auto'}} item>
        <Typography variant="h5" gutterBottom component="div">
          PREVISUALIZACIÓN DEL CONTENIDO
        </Typography>
        <Typography variant="button" gutterBottom component="div">
          Lorem Ipsum is simply dummy text of the printing and typesetting industry.
          Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
          when an unknown printer took a galley of type and scrambled it to make a
          type specimen book.
        </Typography>

        <Button variant="outlined" sx={{ mt: 3 }} onClick={handleEntry}>Ver contenido educativo</Button>
      </Box>
    </Box>
  );
}