    //  Con esta función se ajusta el tamaño de las texturas three js para que conicidad 
    //  con el tamaño de un plano y no pierdan su aspect ratio
    fitImageToPlane: function(texture, widthPlane, heightPlane){
        let imgRatio = texture.image.width / texture.image.height;
        let planeRatio = (widthPlane) / (heightPlane);
        let aspect = imgRatio / planeRatio;

        texture.wrapS = THREE.ClampToEdgeWrapping;
        texture.wrapT = THREE.ClampToEdgeWrapping;

        // si la imágen es más ancha que alta
        if(imgRatio > 1){
            // console.log('ancha')
            texture.repeat.set(1, texture.image.width / texture.image.height);
            texture.offset.y = (texture.repeat.y - 1) / 2 * -1;
            
        }else{
            // console.log('larga')
            texture.repeat.set(texture.image.height / texture.image.width, 1);
            texture.offset.x = (texture.repeat.x - 1) / 2 * -1;
        }

    },

    addToQueue: function(e){
        this.functionsQueue.push(e);
        if(!this.activeFunctionsQueue){
            this.processQueue();
        }
    },

    processQueue: async function(){
        this.activeFunctionsQueue = true;
        // console.log('ejecutando cola');
        if(this.functionsQueue.length > 0){
            const func = this.functionsQueue[0];
            // console.log('ejecutando funcion');
            await func();
            //  Remuevo la primera función - la que acabo de ejecutar en la cola
            this.functionsQueue.shift();
            await new Promise(resolve => setTimeout(resolve, 100));
            this.processQueue()
        }else{
            this.activeFunctionsQueue = false;
        }
    }
  
