import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Toolbar from '@mui/material/Toolbar';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import AdbIcon from '@mui/icons-material/Adb';


//  Componentes React
import AvatarMenu from '../AvatarMenu/AvatarMenu';

const Header = ({children}) => {
    return (
        <AppBar  position="static">
            <Container maxWidth="xl">
                <Toolbar className='interactable-ui' disableGutters sx={{display:'flex', justifyContent:'space-between'}}>
                    {/* <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} /> */}
                    
                    <Box sx={{display:'flex', my:2, justifyContent:'flex-start', alignItems:'center', width:{ xs: '70%', md: '80%' } }}>
                        
                        <CardMedia
                            component="img"
                            sx={{ height:'3rem', width:'3rem',objectFit:'contain' , display: { xs: 'none', md: 'flex' }, mr: 1, p:0 }}
                            image={require('../../../assets/images/logos_loacles/LOGOS_KALEIDO.png')}
                        />
                        <Typography variant="h5" component="h2">
                            MetaLearning VR
                        </Typography>

                    </Box>
                    <Box sx={{display:'flex', justifyContent:'flex-end', alignItems:'center',width:{ xs: '30%', md: '20%' } }}>
                        {children}
                        <AvatarMenu/>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar>
    );
};
export default Header;