import React, { useContext, useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import SettingsAccessibilityIcon from '@mui/icons-material/SettingsAccessibility';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import AddBusinessIcon from '@mui/icons-material/AddBusiness';
import ChatIcon from '@mui/icons-material/Chat';
import GroupIcon from '@mui/icons-material/Group';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';

//  Importacion de componentes propios
import ChatContainer from '../ChatContainer/ChatContainer'
import LocalInfoContainer from '../LocalInfoContainer/LocalInfoContainer';
import LateralDrawer, { AppBar } from '../../LateralDrawer/LateralDrawer';
import ListaUsuarios from '../ListaUsuarios/ListaUsuarios';

// importación del contexto
import { MultiplayerContext } from '../../../../context/MultiplayerContext';

export default function BottomMenu() {

   // Con esta función cargo los datos globales de chat al presionar el botón de chat
   const { chatData, setChatData } = useContext(MultiplayerContext);

  const [openDrawers, setOpenDrawers] = useState({
    drawerChat: false,
    drawerUsers: false,
    drawerLocalInfo: false,
  });

  const handleToggleDrawer = (drawerName, open = !openDrawers[drawerName]) =>{

    //  Se hace una copia del objeto del estado de drawers
    let copyDrawers = { ...openDrawers }
    //  Se pasa a falso el estado de open de cada drawer, excepto el drawer que se activará o desactivará
    Object.keys(copyDrawers).map(function (key, i) {
      if (key !== drawerName) {
        copyDrawers[key] = false;
      }
      else {
        // copyDrawers[key] = !copyDrawers[key];
        copyDrawers[key] = open;
      }

    });
    setOpenDrawers(copyDrawers);
    // setOpenDrawers({ ...openDrawers, [drawerName]: !openDrawers[drawerName] });
  }

  const goToPage = (url) =>{
    if (window.location.pathname != url) {
      window.location.href = url;
    }
  }

  //  Al montarse el componente se espera la escucha del evento de 
  //  colisión con locales para abrir drawer de info locales
  useEffect(() => {
    //  Se escucha al evento de colisión con un teleporter de aframe para abrir el drawer
    document.addEventListener('open_local_info', () => handleToggleDrawer('drawerLocalInfo', true), false);

    //  Se escucha al evento de colisión con un teleporter de aframe para cerrar el drawer
    document.addEventListener('close_local_info', () => handleToggleDrawer('drawerLocalInfo', false), false);

    
    //  Remover el eventlistener al desmontarse el componente
    return () => {
      document.removeEventListener('open_local_info', handleToggleDrawer);
      document.removeEventListener('open_local_info', handleToggleDrawer);
    }

  }, []);

  return (
    <Box sx={{ display: 'flex' }}>
      <AppBar className='interactable-ui' sx={{ top: 'auto', bottom: 0, height: '10vh' }}>
        <Toolbar sx={{ height: '100%', display: 'flex', justifyContent: 'flex-end' }}>

          

          <IconButton color="inherit" onClick={() => goToPage('/personalizacion')}>
            <SettingsAccessibilityIcon />
          </IconButton>

          <IconButton onClick={() => handleToggleDrawer('drawerLocalInfo')} color="inherit">
            <RemoveRedEyeIcon />
          </IconButton>

          <IconButton onClick={() => handleToggleDrawer('drawerUsers')} color="inherit">
            <GroupIcon />
          </IconButton>

          <IconButton onClick={()=>{handleToggleDrawer('drawerChat'); setChatData('global');}} color="inherit">
            <ChatIcon/>
          </IconButton>
        </Toolbar>
      </AppBar>


      {/* Drawer de Chats */}
      <LateralDrawer title= {chatData ==='global'? 'Mensajes de la sala' : 'Mensajes con '+chatData.userName} 
        open={openDrawers.drawerChat} 
        closeDrawer={()=>handleToggleDrawer('drawerChat')}>
        <ChatContainer></ChatContainer>
      </LateralDrawer>

      {/* Drawer de Usuarios */}
      <LateralDrawer title='Usuarios conectados' open={openDrawers.drawerUsers} closeDrawer={()=>handleToggleDrawer('drawerUsers')}>
        <ListaUsuarios handleToggleDrawer={handleToggleDrawer}></ListaUsuarios>
      </LateralDrawer>

      {/* Drawer de información de los locales */}
      <LateralDrawer title='Contenido educativo' open={openDrawers.drawerLocalInfo} closeDrawer={() => handleToggleDrawer('drawerLocalInfo')}>
        <LocalInfoContainer />
      </LateralDrawer>

    </Box>
  );
}